import React, { useState } from "react";
import { PlusOutlined, LockOutlined } from '@ant-design/icons';
import { Radio, Upload, Form, Input, Row, Col } from "antd";

export default function EditUserForm({ isUpdateForm = false }) {
  const [valueRadio, setValueRadio] = useState(1);
  const onChange = (e) => {
    setValueRadio(e.target.value);
  };
  const { TextArea } = Input;
  return (
    <>
      <div className="space30"></div>
      <Row gutter={[24, 24]}>
        <Col className="gutter-row" span={12}>
          <Form.Item
            label="H v tn"
            name="ten_nhan_vien"
            rules={[
              {
                required: true,
                message: "H v tn l trng bt buc.",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="ten_tai_khoan"
            label="Tn ng nhp"
            rules={[
              {
                required: true,
                message: "Tn ng nhp l trng bt buc.",
              },
            ]}
          >
            <Input disabled/>
          </Form.Item>
          <Form.Item
            name="email"
            label="E-mail"
            rules={[
              {
                type: "email",
                message: "Khng ng nh dng E-mail!",
              },
              {
                required: true,
                message: "E-mail l trng bt buc.!",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            name="so_dien_thoai"
            label="S in thoi"
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="gioi_tinh"
            label="Gii tnh"
          >
            <Radio.Group buttonStyle="solid" onChange={onChange} value={valueRadio}>
              <Radio.Button value={1}>Nam</Radio.Button>
              <Radio.Button value={0}>N</Radio.Button>
            </Radio.Group>
          </Form.Item>

        </Col>

        <Col className="gutter-row" span={12}>
          <Form.Item
            name="don_vi"
            label="Ni lm vic"
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="chuc_vu"
            label="Cng vic"
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="dia_chi"
            label="a ch"
          >
            <Input />
          </Form.Item>
          <Form.Item label="Upload" valuePropName="fileList">
            <Upload maxCount={1} action="http://localhost:3000/" listType="picture-card">
              <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}>Upload</div>
              </div>
            </Upload>
          </Form.Item>
        </Col>

      </Row>
      <Form.Item
        name="mat_khau"
        label="Mt khu"
        rules={[
          {
            required: true,
            message: "Please input your Password!",
          },
        ]}
      >
        <Input.Password
          prefix={<LockOutlined className="site-form-item-icon" />}
          type="password"
          placeholder="******"
          autoComplete="off"
        />
      </Form.Item>
      <Form.Item  
        name="gioi_thieu"
        label="Gii thiu">
        <TextArea
        maxLength={1000}
        style={{ height: 120 }}
        placeholder="Gii thiu chi tit"
        />
    </Form.Item>
    </>
  );
}
